  <v-app>
    <div class="loginContainer" v-if="!authStore.isAuth">
      <div class="titleBox">
        <h1>AI-INS Console</h1>
      </div>
      <div class="bodyBox">
        <div class="msgBox" v-if="authStore.isAuth === null">
          <v-progress-circular
            indeterminate
            color="primary"
          ></v-progress-circular>
          <p>로그인 정보를 가져오는 중입니다.</p>
        </div>
        <LoginComponent v-if="authStore.isAuth === false" />
      </div>
    </div>
    <MainFrame v-if="authStore.isAuth === true" />
  </v-app>
</template>

