<script>
import LoginComponent from "@/components/LoginComponent";
import MainFrame from "@/views/MainFrame";

import { useAuthStore } from "@/stories/mainStore.js";
import { auth } from "@/services/user.js";

export default {
  // mounted() {
  //   // console.log("mounted");
  //   // console.log(this.$mqtt);
  //   // this.$mqtt.subscribe("VueMqtt/#");
  // },
  data: () => ({
    selectedItem: null,
    drawer: true,
    group: null,
    defaultPermission: "master",
    permissions: ["master", "visitor"],
    pages: [
      {
        title: "home",
        value: "home",
        to: "/",
      },
      {
        title: "tracking",
        value: "tracking",
        to: "/tracking",
      },
      {
        title: "voyage",
        value: "voyage",
        to: "/voyage",
      },
      {
        title: "data",
        value: "data",
        to: "/data",
      },
      {
        title: "alam",
        value: "alam",
        to: "/alam",
      },
      {
        title: "solution",
        value: "solution",
        to: "/solution",
      },
      {
        title: "diagnostics",
        value: "diagnostics",
        to: "/diagnostics",
      },
      {
        title: "setting",
        value: "setting",
        to: "/setting",
      },
      {
        title: "master",
        value: "master",
        to: "/master",
      },
      {
        title: "custom",
        value: "custom",
        to: "/custom",
      },
    ],
  }),
  components: {
    LoginComponent,
    MainFrame,
  },
  setup() {
    const authStore = useAuthStore();
    return {
      authStore,
      checkAuth: async () => {
        const { data } = await auth();
        if (data.result === "invalid") {
          authStore.setAuth(false);
        } else if (data.result === "valid") {
          authStore.setAuth(true);
        } else {
          console.error("auth error!");
        }
      },
    };
  },
  created() {
    this.checkAuth();
  },
  watch: {
    group() {
      this.drawer = true;
    },
  },
};
</script>
